body {
  background-color: #fff;
  width: 320px; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  border: 2px solid #444444;
  color: #444444;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  text-decoration: none; }

.preview__slogan {
  display: block;
  padding: 72px 37px 150px 37px;
  background-image: url("../img/hero-mobile.jpg");
  font-size: 27px;
  line-height: 30px; }

.preview__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.preview__item {
  padding: 28px 110px 30px 28px;
  font-size: 19px;
  line-height: 24px;
  color: #fff; }
  .preview__item--interior {
    background-color: #63d1bb; }
  .preview__item--toys {
    background-color: #56c3b2; }

.top-item__title {
  margin: 0;
